import router from '@/router'

export function jumpUrl (url) {
  const ok = window.open(url, '_blank')
  if (!ok) {
    window.location.href = url
  }
}

export function filterName (name) {
  if (name === '' || typeof name !== 'string') {
    return name
  }
  return name.replace('《', '').replace('》', '')
}

export function filterTag (name) {
  if (name === '' || typeof name !== 'string') {
    return name
  }
  return name.replace(/<\/?em>/ig, '')
}

export function removeBookNameTag (text) {
  if (text === '' || typeof text !== 'string') {
    return text
  }
  const reg = /《([^》]*)》/ig
  const re = reg.exec(text)
  if (re && re.length >= 2) {
    return re[1]
  }
  return text
}

export const getDefaultIndex = (routeName) => {
  switch (routeName) {
    case 'AdminIndex':
      return '1'
    case 'CategoryConfig':
      return '2'
  }
  return '-1'
}
export const pushRouteByIndex = (index, curRouteName) => {
  console.log(index, curRouteName)
  index *= 1
  switch (index) {
    case 1:
      if (curRouteName !== 'AdminIndex') {
        router.push({ name: 'AdminIndex' })
      }
      break
    case 2:
      if (curRouteName !== 'CategoryConfig') {
        router.push({ name: 'CategoryConfig' })
      }
      break
  }
}

