// 导入自己需要的组件
import {
  Aside,
  Badge,
  Button,
  Card,
  Checkbox,
  CheckboxGroup,
  Col,
  Container,
  Dialog,
  Footer,
  Form,
  FormItem,
  Header,
  Input,
  InputNumber,
  Loading,
  Main,
  Menu,
  MenuItem,
  MenuItemGroup,
  Message,
  MessageBox,
  Notification,
  Option,
  Pagination,
  Popover,
  Radio,
  Row,
  Select,
  Submenu,
  Table,
  TableColumn,
  Tag,
  Tooltip
} from 'element-ui'

const element = {
  install: function (Vue) {
    Vue.use(Pagination)
    Vue.use(Tag)
    Vue.use(Radio)
    Vue.use(Select)
    Vue.use(InputNumber)
    Vue.use(Option)
    Vue.use(Tooltip)
    Vue.use(Table)
    Vue.use(TableColumn)
    Vue.use(Badge)
    Vue.use(MenuItemGroup)
    Vue.use(Submenu)
    Vue.use(MenuItem)
    Vue.use(Menu)
    Vue.use(Aside)
    Vue.use(CheckboxGroup)
    Vue.use(Checkbox)
    Vue.use(Popover)
    Vue.use(Footer)
    Vue.use(Main)
    Vue.use(Container)
    Vue.use(Header)
    Vue.use(Input)
    Vue.use(FormItem)
    Vue.use(Form)
    Vue.use(Button)
    Vue.use(Card)
    Vue.use(Row)
    Vue.use(Col)
    Vue.use(Loading.directive)
    Vue.prototype.$confirm = MessageBox.confirm
    Vue.prototype.$notify = Notification
    Vue.prototype.$message = Message
    Vue.use(Dialog)
  }
}
export default element
