import http from '@/api/http'

// 修改密码
export const reqUpdatePass = (value) => (http.post('/user/v1/update_pass', { ...value }))
export const reqUserInfo = async () => {
  return http.get('/user/v1/info')
}

const reqLogin = (username, password) => {
  return http.post('/user/v1/login', {
    username,
    password
  })
}

export {
  reqLogin
}
