const USERNAME = 'username'
const TOKEN = 'token'
const ID = 'id'
const SUBSCRIBE_ID = 'subscribe_id'

export const getValue = (key, defaultValue = '') => {
  return localStorage.getItem(key) || defaultValue
}

export const setValue = (key, value) => {
  localStorage.setItem(key, value)
}

export const setUsername = (info) => {
  setValue(USERNAME, info)
}

export const getUsername = () => {
  try {
    return getValue(USERNAME)
  } catch (e) {
    return ''
  }
}

export const setToken = (token) => {
  setValue(TOKEN, token)
}

export const getToken = () => {
  return getValue(TOKEN)
}
export const setID = (id) => {
  setValue(ID, id)
}

export const getID = () => {
  return getValue(ID)
}

//= =======设置订阅ID
export const setSubscribeId = (id) => {
  setValue(SUBSCRIBE_ID, id)
}
export const getSubscribeId = () => {
  return getValue(SUBSCRIBE_ID)
}

export const getUserToken = () => {
  return getValue(TOKEN)
}

export const getAllValues = () => {
  const l = localStorage.length
  const obj = {}
  for (let i = 0; i < l; i++) {
    const key = localStorage.key(i)
    obj[key] = getValue(key)
  }
  return obj
}

// 清空登录信息
export const clearLoginInfo = () => {
  localStorage.removeItem(USERNAME)
}
