import { setID, setToken, setUsername } from '@/config/data'

export default {

  changeKw (state, val) {
    state.kw = val
  },
  changeUser (state, val) {
    setUsername(val.username)
    setToken(val.token)
    setID(val.id)
    state.user = val
  },
  changeWhat (state, val) {
    state.what = val
  }
}
