import Vue from 'vue'
import VueRouter from 'vue-router'
import Admin from '@/views/Admin'

Vue.use(VueRouter)

const routes = [
  {
    path: '/search',
    name: 'Search',
    component: () => import('@/views/Search')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/Login')
  },
  {
    path: '/admin',
    component: Admin,
    children: [
      {
        path: '/',
        name: 'AdminIndex',
        component: () => import(/* webpackChunkName: "Statistics" */'@/views/admin/Statistics'),
        meta: {
          title: '管理后台',
          auth: true
        }
      },
      {

        path: 'updatePass',
        name: 'UpdatePass',
        component: () => import(/* webpackChunkName: "UpdatePass" */'@/views/admin/UpdatePass'),
        meta: {
          title: '修改密码',
          auth: true
        }
      }
    ]
  },
  {
    path: '/',
    component: () => import('@/views/Home'),
    children: [
      {
        path: '/',
        name: 'Home',
        component: () => import('@/components/home/SearchBox')
      },
      {
        path: '/about',
        name: 'About',
        component: () => import('@/components/home/About')
      },
      {
        path: '/ban',
        name: 'Ban',
        component: () => import('@/components/home/Ban')
      }
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
